import React from "react"
import { useEffect, useState } from "react"
import { Swiper, SwiperSlide } from "swiper/react"
import { Navigation, Pagination, EffectCoverflow } from "swiper/modules"
import FeaturedProject from "../Features/FeaturedProject"
import "swiper/css"
import "swiper/css/navigation"
import "swiper/css/pagination"
import "swiper/css/effect-coverflow"
import "./FeaturesSlider.css"


const FeaturesSlider = ({ items }) => {
  // console.log(window.innerWidth, ' mobile? ', window.innerWidth <= 1024)
  const [isMobile, setIsMobile] = useState(false)
  // console.log(' mobile? ', isMobile)

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.matchMedia("(max-width: 1024px)").matches)
    }

    window.addEventListener("resize", handleResize)
    handleResize()

    return () => {
      window.removeEventListener("resize", handleResize)
    }
  }, [])

  return (
    <>
      {isMobile ? (
        <div className="triple-slider-container">
          <Swiper
            modules={[Navigation, Pagination, EffectCoverflow]}
            spaceBetween={-100} // Negative value for overlap
            slidesPerView={1}
            centeredSlides={true} // Center the active slide
            initialSlide={1}
            pagination={{ clickable: true }}
            loop={false}
            effect="coverflow"
            coverflowEffect={{
              rotate: 0,
              stretch: 0,
              depth: 200,
              modifier: 1,
              slideShadows: false,
            }}
            breakpoints={{
              768: {
                slidesPerView: 3, // 3 slides visible for screen width >= 768px
                spaceBetween: -100, // Adjust space between slides
              }
            }}
          >
            {items.map((item, index) => (
              <SwiperSlide key={index}>
                <FeaturedProject feature={item} />
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      ) : (
        <div className="container container__tight container__scroll">
          {items.map((node, index) => (
            <FeaturedProject feature={node} key={index} />
          ))}
        </div>
      )}
    </>
  )
}

export default FeaturesSlider
